import React from 'react';
import {styleSheetJson} from "../react-app-env";
import PowerSwitch from "../coponent/common/controller/powerSwitch";
import xhr from "../common/xhr";
import {Empty, Spin} from "antd";
import echart from 'echarts';
import moment from 'moment'

export default class Home extends React.Component {
    state = {
        overView: {
            airConditioning: 0,
            office: 0,
            dorm: 0,
            other: 0,
            total: 0
        },
        selected: '',
        chunk: [],
        loading: false,
        totalUsed: 0,
        change: 0,
        onlineCount: 0,
        offLineCount: 0,
        emptyChart: false,
        chartLoading: true
    };
    chart?: echart.ECharts;

    render(): React.ReactElement<any, string | React.JSXElementConstructor<any>> | string | number | {} | React.ReactNodeArray | React.ReactPortal | boolean | null | undefined {
        return (
            <div style={styleSheet.home}>
                <div style={styleSheet.charts}>
                    <div style={styleSheet.circleBlock}>
                        <div style={styleSheet.title}>用电概览</div>
                        <div style={{
                            width: 60,
                            height: 60,
                            borderRadius: 30,
                            backgroundColor: '#FCCD14',
                            position: "absolute",
                            left: 'calc(50% - 20vh)',
                            top: 'calc(50% - 9vh)',
                        }}/>
                        <div style={{
                            width: 45,
                            height: 45,
                            borderRadius: '50%',
                            backgroundColor: '#00d05e',
                            position: "absolute",
                            left: 'calc(50% + 12vh)',
                            top: 'calc(50% - 13vh)',
                        }}/>
                        <div style={{
                            width: 28,
                            height: 28,
                            borderRadius: 14,
                            backgroundColor: '#2697f3',
                            position: "absolute",
                            left: 'calc(50% - 16vh)',
                            top: 'calc(50% + 11vh)',
                        }}/>
                        <div style={{
                            width: 28,
                            height: 28,
                            borderRadius: 14,
                            backgroundColor: '#ff525d',
                            position: "absolute",
                            left: 'calc(50% + 13vh)',
                            top: 'calc(50% + 4vh)',
                        }}/>
                        <div style={{
                            color: '#FCCD14', width: 180, height: 100, position: "absolute", left: 'calc(50% - 36vh)',
                            top: 'calc(50% - 9vh)', cursor: "pointer"
                        }} onClick={this.getSwitch.bind(this, "空调")}>
                            <div style={{width: 140, borderBottom: '1px solid #FCCD14'}}>
                                空调
                                <span style={{float: "right"}}>75%</span>
                            </div>
                            <p style={{fontSize: 44, marginTop: 14}}>
                                35453
                            </p>
                        </div>
                        <div style={{
                            color: '#00d05e', width: 180, height: 100, position: "absolute", left: 'calc(50% + 20vh)',
                            top: 'calc(50% - 13vh)', cursor: "pointer"
                        }} onClick={this.getSwitch.bind(this, "办公区")}>
                            <div style={{width: 140, borderBottom: '1px solid #00d05e'}}>
                                办公区
                                <span style={{float: "right"}}>35%</span>
                            </div>
                            <p style={{fontSize: 44, marginTop: 14}}>
                                {35453 * 0.35}
                            </p>
                        </div>
                        <div style={{
                            color: '#2697f3', width: 180, height: 100, position: "absolute",
                            left: 'calc(50% - 34vh)',
                            top: 'calc(50% + 11vh)', cursor: "pointer"
                        }} onClick={this.getSwitch.bind(this, "其它")}>
                            <div style={{width: 140, borderBottom: '1px solid #2697f3'}}>
                                其它
                                <span style={{float: "right"}}>25%</span>
                            </div>
                            <p style={{fontSize: 44, marginTop: 14}}>
                                35453
                            </p>
                        </div>
                        <div style={{
                            color: '#ff525d', width: 180, height: 100, position: "absolute",
                            left: 'calc(50% + 18vh)',
                            top: 'calc(50% + 4vh)', cursor: "pointer"
                        }} onClick={this.getSwitch.bind(this, "宿舍")}>
                            <div style={{width: 140, borderBottom: '1px solid #ff525d'}}>
                                宿舍
                                <span style={{float: "right"}}>65%</span>
                            </div>
                            <p style={{fontSize: 44, marginTop: 14}}>
                                {35453 * 0.65}
                            </p>
                        </div>
                    </div>
                    <div style={styleSheet.block}>
                        <div style={{...styleSheet.title, zIndex: 9999}}>用电对比</div>
                        <div id="map" style={{
                            height: '100%',
                            marginRight: 80,
                            visibility: !this.state.emptyChart && !this.state.chartLoading ? "visible" : "hidden",
                            marginLeft: 175
                        }}/>
                        {
                            this.state.emptyChart && <Empty style={styleSheet.emptyBlock}
															description={<span style={{color: '#FFF'}}>没有记录</span>}/>
                        }
                        {
                            this.state.chartLoading && <Spin style={styleSheet.emptyBlock}/>
                        }
                        <ul style={styleSheet.compare}>
                            <li>普通用电量</li>
                            <li style={{marginBottom: 12}}>35422kW·h</li>
                            <li>智能用电量</li>
                            <li style={{marginBottom: 12}}>{(35422*0.8).toFixed(2)}kW·h</li>
                            <li>变化</li>
                            <li><div style={{
                                display: "inline-block",
                                backgroundColor: "green",
                                padding: '0 6px',
                                borderRadius: 16,
                                fontSize: 15
                            }}>↓ 20%</div></li>
                        </ul>
                    </div>
                </div>
                <div style={styleSheet.splitLine}/>
                <div style={styleSheet.controllers}>
                    {
                        this.state.loading || (
                            <div style={styleSheet.powerMain}>
                                <img src={require("../resource/common/air-conditioning-main.png")} alt=""/>
                                <span style={styleSheet.powerMainName}>{this.state.selected}</span>
                                <div style={{
                                    display: "flex",
                                    flexGrow: 1,
                                    flexDirection: 'row',
                                    alignItems: 'center',
                                    justifyContent: 'space-around'
                                }}>
                                    <span>{this.state.totalUsed}kW·h</span>
                                    <span>{this.state.change}</span>
                                    <ul style={{marginBottom: 0, width: 80}}>
                                        <li>在线：{this.state.onlineCount}</li>
                                        <li>离线：{this.state.offLineCount}</li>
                                    </ul>
                                </div>
                            </div>
                        )
                    }
                    <div style={{flexGrow: 1, overflowY: 'auto', overflowX: "hidden", height: '100%'}}>
                        {
                            this.state.loading ?
                                <Spin/> :
                                null
                        }
                        {
                            (this.state.chunk.length === 0 && !this.state.loading) ?
                                <Empty style={styleSheet.emptyBlock}
                                       description={<span style={{color: '#FFF'}}>没有数据</span>}/> :
                                this.state.chunk.map((t: any) => <PowerSwitch
                                    onClick={this.setChart.bind(this, t.id,t.name)} {...t} key={t.id}/>)
                        }
                    </div>
                </div>
            </div>
        )
    }

    getSwitch(value: string = '') {
        this.setState({
            loading: true,
            chunk: []
        });
        let params;
        if(value==='其它'){
            params = {
                'name__contains!': '空调',
                devicetype:24
            }
        } else if(value==='办公区'){
            params = {
                'name__contains!': '宿舍',
                devicetype:24
            }
        } else {
            params = {
                name__contains: value,
                devicetype:24
            }
        }
        xhr({
            url: '/device/rest/device/',
            params
        }).then(({data}) => {
            let totalUsed = 0;
            let change = '↑ 25%';
            let onlineCount = 0;
            let chunk: any[] = [];
            data.results.forEach((t: any) => {
                const temp = {
                    name: t.name,
                    online: t.lastdata['在线'] === '是',
                    power: t.lastdata["开关状态"] === "true",
                    used: t.lastdata["用电量"],
                    id: t.id
                };
                totalUsed += (temp.used ? temp.used * 1 : 0);
                temp.online && onlineCount++;
                chunk.push(temp)
            });
            this.setState({
                loading: false,
                totalUsed: totalUsed.toFixed(2),
                change,
                onlineCount,
                offLineCount: data.results.length - onlineCount,
                chunk,
                selected: value || "所有"
            })
        })
    }

    setChart(id: number,name: string) {
        this.setState({
            emptyChart: false,
            chartLoading: true
        });
        xhr({
            url: '/device/rest/sensordata/',
            params: {
                sensor__device__in: id,
                stime__gte: moment(new Date()).format('YYYY-MM-DD') + ' 00:00:00'
            }
        }).then(({data}) => {
            if (data.length === 0) {
                this.setState({
                    emptyChart: true,
                    chartLoading: false
                });
                return
            }
            let option: echart.EChartOption = {
                color: ['#FCCD14', "#2697f3"],
                legend: {
                    top: 0,
                    right: 60,
                    textStyle: {
                        color:"#FFF"
                    }
                },
                xAxis: {
                    type: 'time',
                    axisLine: {
                        show: false
                    },
                    axisLabel: {
                        color: '#FFF'
                    },
                    splitLine: {
                        show: false
                    }
                },
                yAxis: {
                    type: 'value',
                    axisLine: {
                        show: false
                    },
                    axisLabel: {
                        color: "#FFF"
                    },
                    splitLine: {
                        show: false
                    },
                },
                grid: {
                    top: 120,
                    left: 30,
                    right: 60
                },
                // @ts-ignore
                series: [{
                    data: data.map((t: any) => [t.stime, t.data]),
                    type: 'line',
                    areaStyle: {
                        color:  {
                            type: 'linear',
                            x: 0,
                            y: 0,
                            x2: 0,
                            y2: 1,
                            colorStops: [{
                                offset: 1, color: 'rgba(128, 128, 128, 0)'
                            }, {
                                offset: 0, color: '#FCCD14'
                            }],
                            global: false
                        }
                    },
                    name,
                    lineStyle: {
                        color: '#FCCD14'
                    }
                }]
            };
            if (this.chart) {
                this.chart.setOption(option, !0);
                this.setState({
                    chartLoading: false
                })
            }
        })
    }
    setFakeChart(){
        let chunk1 = [];
        let chunk2 = [];
        let time = moment(new Date()).format("YYYY-MM-DD") + 'T';
        for (let i = 0;i<(new Date().getHours());i++){
            const value = Number((10 * Math.random()).toFixed(2));
            chunk1.push([time+i+':00:00',value]);
            chunk2.push([time+i+':00:00',value*0.8]);
        }

        let option: echart.EChartOption = {
            color: ['#FCCD14', "#2697f3"],
            legend: {
                top: 0,
                right: 60,
                textStyle: {
                    color:"#FFF"
                }
            },
            xAxis: {
                type: 'time',
                axisLine: {
                    show: false
                },
                axisLabel: {
                    color: '#FFF'
                },
                splitLine: {
                    show: false
                }
            },
            yAxis: {
                type: 'value',
                axisLine: {
                    show: false
                },
                axisLabel: {
                    color: "#FFF"
                },
                splitLine: {
                    show: false
                },
            },
            grid: {
                top: 120,
                left: 30,
                right: 60
            },
            // @ts-ignore
            series: [{
                data: chunk1,
                type: 'line',
                areaStyle: {
                    color:  {
                        type: 'linear',
                        x: 0,
                        y: 0,
                        x2: 0,
                        y2: 1,
                        colorStops: [{
                            offset: 1, color: 'rgba(128, 128, 128, 0)'
                        }, {
                            offset: 0, color: '#FCCD14'
                        }],
                        global: false
                    }
                },
                name: '节能前',
            },
        {
                data: chunk2,
                type: 'line',
                areaStyle: {
                    color:  {
                        type: 'linear',
                        x: 0,
                        y: 0,
                        x2: 0,
                        y2: 1,
                        colorStops: [{
                            offset: 1, color: 'rgba(128, 128, 128, 0)'
                        }, {
                            offset: 0, color: '#2697f3'
                        }],
                        global: false
                    }
                },
                name: '节能后',
            }]
        };
        this.setState({
            chartLoading: false,
            emptyChart: false
        });
        // @ts-ignore
        this.chart.setOption(option,true)
    }
    componentDidMount(): void {
        // @ts-ignore
        this.chart = echart.init(document.getElementById('map'));
        this.getSwitch('');
        this.setFakeChart();
    }
}
const styleSheet: styleSheetJson = {
    home: {
        width: "100%",
        height: "100%",
        display: 'flex',
        flexDirection: 'row'
    },
    title: {
        backgroundImage: `url(${require('../resource/desktop/titleBox.png')})`,
        paddingTop: 12,
        paddingBottom: 12,
        fontSize: 20,
        color: "white",
        textAlign: 'center',
        width: 170,
        height: 60,
        backgroundRepeat: "no-repeat",
        position: "absolute"
    },
    charts: {
        flexGrow: 1,
        height: '100%',
        padding: '30px 50px'
    },
    splitLine: {
        position: 'absolute',
        height: "100%",
        width: 2,
        right: 440,
        top: 0,
        backgroundImage: `url(${require('../resource/desktop/splitLine.png')})`,
        backgroundSize: '100% 100%'
    },
    controllers: {
        width: 440,
        height: '100%',
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        paddingTop: 24,
        paddingBottom: 24
    },
    powerMain: {
        backgroundImage: `url(${require("../resource/desktop/powerMain.png")})`,
        backgroundSize: '100% 100%',
        width: 330,
        height: 60,
        display: 'flex',
        flexDirection: 'row',
        alignItems: 'center',
        paddingLeft: 16,
        paddingRight: 16,
        marginBottom: 12,
        color: 'white'
    },
    powerMainName: {
        flexGrow: 0,
        color: '#FFF',
        fontSize: 20,
        marginLeft: 12
    },
    block: {
        height: '50%'
    },
    circleBlock: {
        height: '50%',
        backgroundRepeat: "no-repeat",
        backgroundPosition: 'center',
        backgroundSize: 'auto 100%',
        backgroundImage: 'url(' + require("../resource/desktop/homeCircle.png") + ')'
    },
    emptyBlock: {
        display: "flex",
        flexDirection: "column",
        width: '100%',
        height: '100%',
        justifyContent: 'center',
        top: 0,
        position: "absolute",
        alignItems: 'center'
    },
    compare: {
        position: "absolute",
        right: 12,
        bottom: 50,
        color: '#FFF',
        fontSize: 18
    }
};
