import React from 'react';
import PropType from 'prop-types'
import {styleSheetJson} from "../../../react-app-env";
import {Switch} from "antd";
import xhr from "../../../common/xhr";

export default class PowerSwitch extends React.Component<Props, {}> {
    static defaultProps = {
        used: 0,
        power: false,
        online: false
    };
    static propTypes: {
        name: PropType.Requireable<string>;
        used: PropType.Requireable<number | string>;
        power: PropType.Requireable<boolean>;
        online: PropType.Requireable<boolean>;
        clickHandle: PropType.Requireable<Function>;
        id: PropType.Requireable<number>
    };
    state = {
        loading: false,
        power: this.props.power
    };

    render(): React.ReactElement<any, string | React.JSXElementConstructor<any>> | string | number | {} | React.ReactNodeArray | React.ReactPortal | boolean | null | undefined {
        return (
            <div style={styleSheet.root} onClick={this.props.onClick}>
                <img src={require("../../../resource/common/air-conditioning.png")} alt="" style={{flexGrow: 0}}
                     width='35' height='27'/>
                <ul style={{...styleSheet.list, flexGrow: 1}}>
                    <li>{this.props.name}</li>
                    <li style={{fontSize: 14}}>{this.props.used}kW·h</li>
                </ul>
                <ul style={styleSheet.list}>
                    <li><Switch checked={this.state.power} checkedChildren="ON" loading={this.state.loading}
                                unCheckedChildren="OFF" onClick={this.handleClick.bind(this)} size='small'/></li>
                    <li>{this.props.online ? '在线' : "离线"}</li>
                </ul>
            </div>
        )
    }

    handleClick(): void {
        this.setState({
            loading: true
        }, () => {
            xhr({
                url: `/device/rest/device/${this.props.id}/switch_${this.state.power ? "off" : "on"}/`
            }).then(() => {
                this.setState({
                    loading: false,
                    power: !this.state.power
                })
            })
        })
    }
}
const styleSheet: styleSheetJson = {
    root: {
        backgroundImage: `url(${require('../../../resource/common/controllerBox.png')})`,
        width: 280,
        height: 70,
        marginBottom: 16,
        backgroundSize: '100% 100%',
        display: 'flex',
        flexDirection: "row",
        alignItems: 'center',
        paddingLeft: 24,
        paddingRight: 24,
        cursor: "pointer"
    },
    list: {
        color: '#FFF',
        fontSize: 16,
        fontFamily: 'PingFang-SC-Regular',
        textAlign: "left",
        paddingInlineStart: 16,
        marginBottom: 0
    }
};

interface Props {
    name: string,
    used: number | string,
    power: boolean,
    online: boolean,
    id: number,
    onClick?:(event:any)=>void
}
